<template>
  <div class="main">
    <div class="header">
      <router-link to="/game" class="return"></router-link>
      
    </div>

    <div class="content">
      <div v-show="type===1" v-for="(v,i) in zx" @click="show(i)" :class="` ${i===active?'active':'card'}`">
        <div class="v-id"></div>
        <div class="slider"></div>
      </div>
      <div v-show="type===2" v-for="(v,i) in mx" @click="show(i)" :class="` ${i===active?'active':'card'}`">
        <div class="v-id"></div>
        <div class="slider"></div>
      </div>
    </div>

    <div class="notice">
      <div class="title">
        <h1>///</h1>
        <h2>MATTERS NEDING <p>ATTENTION</p></h2>
        <div class="divider"></div>
      </div>
      <div class="min">
        <p>1.</p>
        <p>2.</p>
      </div>
    </div>
    <div class="footer">
      <div @click="setActive(1)" :class="{active:type==1}"></div>
      <div @click="setActive(2)" :class="{active:type==2}"></div>
    </div>
    <div :class="`mask container ${mask?'active':''}`" v-show="mask">
      <div class="mask-con">
        <div class="subject">
          <div class="titles">
            <h2 v-text="maskTitle==='truth'?' ':''"></h2>
            <p v-text="maskTitle==='truth'?'Crazy adventure ':'The truth of your heart'"></p>
          </div>
          <div class="contents" v-text="maskContent"></div>
          <div class="button" @click="once"></div>
        </div>
        <div class="game-close" @click="close"></div>
      </div>
    </div>

  </div>
</template>
<style lang="scss" src="@/assets/style/game01.scss" scoped></style>

<script>
  var dict = {};
  var dict2 = {};
  export default{
    name: 'true',
    data: function () {
      return {
        type: 1,
        active: null,
        zx: [],
        mx: [],
        mask: false,
        maskTitle: '',
        maskContent: '',
      }
    },
    methods: {
      once: function () {
        this.mask = false;
      },
      close: function () {
        this.mask = false;
      },
      setActive: function (type) {
        type === 2 ? this.maskTitle = 'truth' : this.maskTitle = 'dare';

        this.type = type;
        this.active = null;
      },
      show: function (i, v) {
        this.active = i;
        this.open();
      },
      open: function (v) {

        if (this.type === 1) {
          if (Object.keys(dict).length >= 9) {
            dict = {};
          }
          do {
            var index = Math.floor(Math.random() * 9);
          } while (dict[index]);
          dict[index] = true;

          if (v === '') {
            this.mask = true;
            this.maskContent = '';
          } else {
            this.mask = true;
            this.maskContent = this.zx[index].title;
          }
//          alert();
        } else if (this.type === 2) {
          if (Object.keys(dict2).length >= 9) {
            dict2 = {};
          }
          do {
            var index = Math.floor(Math.random() * 9);
          } while (dict2[index]);
          dict2[index] = true;
          if (v === '') {
            this.mask = true;
            this.maskContent = '';
          } else {
            this.mask = true;
            this.maskContent = this.mx[index].title;
          }
        }
      }
    },
    mounted: function () {
      var data = {
        zx: [
          {id: 1, title: '?'},
          {id: 2, title: '?'},
          {id: 3, title: ''},
          {id: 4, title: ''},
          {id: 5, title: 'ta '},
          {id: 6, title: ''},
          {id: 7, title: ''},
          {id: 8, title: '?'},
          {id: 9, title: ''}
        ],
        mx: [
          {id: 1, title: ''},
          {id: 2, title: ''},
          {id: 3, title: ''},
          {id: 4, title: '10'},
          {id: 5, title: ''},
          {id: 6, title: ''},
          {id: 7, title: ';'},
          {id: 8, title: '"";'},
          {id: 9, title: ''}
        ]
      };
      this.zx = data.zx;
      this.mx = data.mx;
    }
  }
</script>



