import Vue from 'vue'
import Router from 'vue-router'

//----------------------  --------------------
import Classify from '@/components/Classify'
import Link from '@/components/Link'

//----------------------  -------------------
import Game from '@/components/Game'
import Game01 from '@/components/Game01'
//----------------------  -------------------
import Me from '@/components/Me'

//----------------------  ----------------------
import Store from '@/components/Store'
import Choised from '@/components/Choised'
import Success from '@/components/Success'

//---------------------  ---------------------
import Rank from '@/components/Ranking-list'
import Qrank from '@/components/Quarter-rank'
import Yrank from '@/components/Year-rank'
// ------------------------------------------
import Recommend from '@/components/Recommend'
import SongsList from '@/components/Songslist'
import Popular from '@/components/Popular'
//---------------------  ----------------------
import Artist from '@/components/Artist'
import Singer from '@/components/Singer'
import ArtistClassify from '@/components/Artist-classifiction'
import Songs from '@/components/Songs'
import Lyrics from '@/components/Lyrics'
import Checked from "@/components/Checked"


Vue.use(Router);
export default new Router({

  mode: 'history',
  routes: [

    //----------------  ------------------
    {
      path: '/',
      name: 'classify',
      component: Classify
    },


    // ------------------  ---------------------
    {
      path: '/link',
      name: 'link',
      component: Link
    },


    //-----------------  ------------------
    {
      path: '/game',
      name: 'game',
      component: Game
    },
    //------------------------------
    {
      path: '/game/game01',
      name: 'game01',
      component: Game01
    },


    //-----------------  ------------------
    {
      path: '/store',
      name: 'store',
      component: Store
    },
    //-----------------  ------------------
    {
      path: "/store/choised",
      name: "Choised",
      component: Choised
    },
    //--------------------------------------
    {
      path: "/store/success",
      name: "Success",
      component: Success
    },


    //----------------  ----------------
    {
      path: '/artist',
      name: 'artist',
      component: Artist,
      children: [
        // 
        {
          path: 'singers',
          name: 'singers',
          component: ArtistClassify
        },
        // 
        {
          path: 'singer',
          name: 'singer',
          component: Singer
        },
        // 
        {
          path: 'rank',
          name: 'rank',
          component: Qrank
        },
        // 
        {
          path: 'qrank',
          name: 'qrank',
          component: Qrank
        },
        // 
        {
          path: 'yrank',
          name: 'yrank',
          component: Qrank
        },
        // ---------------------------------------
        {
          path: 'recommend',
          name: 'recommend',
          component: Recommend
        },
        //----------------  -----------------
        {
          path: 'popular',
          name: 'popular',
          component: Popular
        },
      ]
    },
    // ---------------------------------------
    {
      path: '/songslist',
      name: 'songslist',
      component: SongsList
    },
    //----------------  -----------------
    {
      path: '/songs/:id',
      name: 'songs',
      component: Songs,
    },
    //----------------  -----------------
    {
      path: '/checked',
      name: 'checked',
      component: Checked,
    },

    //-----------------  ------------------
    {
      path: '/songs/:id/:num/lyrics',
      name: 'lyrics',
      component: Lyrics
    },


    // ---------------------------------------
    {
      path: '/me',
      name: 'me',
      component: Me
    }
  ]
})



